<template>
  <section class="main-wrapper">
    <header class="header-wrapper">
    <div class="search bar6">
        <div class="form-div">
            <input type="text" v-model="inpuTxt" v-on:input="change" placeholder="">
            <button type="submit" @click="doSearch()"></button>
        </div>
    </div>
      <div class="search-div">
        <ul class="searchResult">
				 <li v-for="suggestTxt in suggestResults" @click="doSuggest(suggestTxt.name)">{{suggestTxt.name}}<span>{{suggestTxt.address}}</span></li>
			 </ul>
      </div>
      <div class="search-div" v-show="searchPanel">
			 <span class="close-ico" @click="closeSearchPanel()"><img src="data:image/png;base64,"></span> 
			 <ul class="searchResult">
				 <li v-for="searchTxt in searchResults" @click="addPointIco(searchTxt)">{{searchTxt.name}}<span>{{searchTxt.address}}</span></li>
			 </ul>
		 </div>
    </header>
    <main class="map-wrapper">
      <div id="map"></div>
    </main>       
  </section>
</template>

<script>
import "maptalks/dist/maptalks.css";
import * as maptalks from "maptalks";
import Axios from "axios";

let myMap, markerLyr, self, timeInteval;
export default {
  name: "vueMapSearch",
  props: {
    x: {
      type: Number,
      default: 114.316200103
    },
    y: {
      type: Number,
      default: 30.5810841269
    },
    xyChange: {
      type: Function
    }
  },
  data() {
    return {
      olmap: null,
      inpuTxt: "",
      searchPanel: false,
      searchResults: [],
      suggestTxt: "",
      suggestResults: []
    };
  },
  methods: {
    doSearch() {
      search(this.inpuTxt);
    },
    doSuggest(txt) {
      this.inpuTxt = txt;
      this.suggestResults = [];
      search(txt);
    },
    addPointIco(obj) {
      var xys = obj.lonlat.split(" ");
      drawPoint(xys[0], xys[1]);
    },
    closeSearchPanel() {
      this.searchPanel = false;
    },
    change() {
      timeInteval && clearTimeout(timeInteval);
      timeInteval = setTimeout(() => {
        suggest(self.inpuTxt);
      }, 200);
    }
  },
  mounted() {
    self = self || this;
    this.$nextTick(function() {
      initMap();
    });
  }
};
function suggest(txt) {
  let url = "http://map.tianditu.com/data/suggest";
  let postString =
    'postStr={"yingjiType":1,"sourceType":0,"keyWord":"{0}","level":12,"mapBound":"{1}","queryType":"4","count":"10","start":"0"}';
  let searchExtent = "110.58838,29.25286,118.09204,31.98012";
  postString = postString.replace("{0}", txt);
  postString = postString.replace("{1}", searchExtent);
  Axios.get(url + "?" + postString)
    .then(function(response) {
      resolveSuggestResult(response.data);
    })
    .catch(function(error) {
      console.log(error);
    });
}
function resolveSuggestResult(obj) {
  self.suggestResults = obj.suggests || [];
}
function search(txt) {
  self.searchPanel = false;
  let url = "http://map.tianditu.com/query.shtml";
  let postString =
    'postStr={"keyWord":"{0}","level":"12","mapBound":"{1}","queryType":"1","count":"10","start":"0"}&type=query';
  let searchExtent = "110.58838,29.25286,118.09204,31.98012";
  postString = postString.replace("{0}", txt);
  postString = postString.replace("{1}", searchExtent);
  Axios.get(url + "?" + postString)
    .then(function(response) {
      resolveResult(response.data);
    })
    .catch(function(error) {
      console.log(error);
    });
}
function resolveResult(json) {
  var noResult = true;
  if (json.area) {
    noResult = false;
    var xys = json.area.lonlat.split(",");
    drawPoint(xys[0], xys[1]);
  }
  if (json.pois) {
    noResult = false;
    if (json.pois.length === 1) {
      self.addPointIco(json.pois[0]);
    } else {
      self.searchResults = json.pois;
      self.searchPanel = true;
    }
  }
  if (noResult) {
    alert("");
  }
}
function drawPoint(x, y) {
  x = parseFloat(x);
  y = parseFloat(y);
  let geometry = markerLyr.getGeometryById("outPositionIco");
  geometry && geometry.remove();
  new maptalks.Marker([x, y], {
    id: "outPositionIco",
    draggable: false,
    symbol: {
      markerFile:
        "data:image/png;base64,",
      markerWidth: 32,
      markerHeight: 38,
      markerDx: 0,
      markerDy: 0,
      markerOpacity: 1
    }
  }).addTo(markerLyr);
  myMap.setCenter([x, y]);
  notify("center", { x: x, y: y });
}
function createPointStyle(src, img) {
  return new ol.style.Style({
    image: new ol.style.Icon({
      anchorXUnits: "pixels",
      anchorYUnits: "pixels",
      anchor: [16, 38],
      src: src,
      img: img,
      imgSize: img ? [img.width, img.height] : undefined
    })
  });
}
function initMap() {
  myMap = new maptalks.Map("map", {
    center: [self.x, self.y],
    zoom: 12,
    minZoom: 1,
    maxZoom: 18,
    spatialReference: {
      projection: "EPSG:4326"
    },
    baseLayer: new maptalks.TileLayer("base", {
      tileSystem: [1, -1, -180, 90],
      urlTemplate:
        "http://t{s}.tianditu.com/DataServer?T=vec_c&x={x}&y={y}&l={z}",
      subdomains: ["1", "2", "3", "4", "5"],
      attribution:
        '&copy; <a target="_blank" href="http://www.tianditu.cn">Tianditu</a>'
    }),
    layers: [
      new maptalks.TileLayer("road", {
        urlTemplate:
          "http://t{s}.tianditu.com/DataServer?T=cva_c&x={x}&y={y}&l={z}",
        subdomains: ["1", "2", "3", "4", "5"],
        opacity: 1
      })
    ]
  });
  markerLyr = new maptalks.VectorLayer("vector").addTo(myMap);
  myMap.on("click", function(evt) {
    let coordinate = evt.coordinate;
    notify("click", coordinate);
  });
  drawPoint(self.x, self.y);
}
function notify(type, coordinate, opts) {
  self.xyChange({
    type: type,
    coordinate: coordinate,
    opts: {}
  });
}
</script>

<style>
.form-div {
  position: relative;
  width: 100%;
  background-color: #fff;
}
input,
button {
  border: none;
  outline: none;
}
input {
  width: 100%;
  height: 42px;
  padding-left: 13px;
}
button {
  height: 42px;
  width: 42px;
  cursor: pointer;
  position: absolute;
}
/*6*/

.bar6 input {
  border: 2px solid #08c;
  border-radius: 5px;
  background: transparent;
  top: 0;
  right: 0;
  font-size: 18px;
  box-sizing: border-box;
}
.bar6 button {
  background: #08c;
  border-radius: 0 5px 5px 0;
  width: 60px;
  top: 0;
  right: 0;
}
.bar6 button:before {
  content: "";
  font-size: 13px;
  color: #f9f0da;
}
main,
section {
  display: block;
}
.main-wrapper {
  width: 100vw;
  height: 100vh;
  position: relative;
}
.header-wrapper,
.map-wrapper {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
}
.header-wrapper {
  position: absolute;
  z-index: 1;
  width: 100%;
  height: 46px;
  top: 0;
}
#map {
  width: 100%;
  height: 100%;
}
.maptalks-attribution {
  display: none;
}
.search-div {
  position: relative;
  background-color: white;
}
.search-div > span > img {
  display: block;
}
.searchResult {
  margin: 0;
  padding: 0;
  max-height: 280px;
  overflow-y: auto;
}
.searchResult > li {
  cursor: pointer;
  padding: 2px 10px;
  font-size: 15px;
  list-style-type: none;
}
.searchResult > li:hover {
  background-color: #f5f5f5;
}
.searchResult > li > span {
  color: #ccc;
  font-size: 13px;
}
.close-ico {
  position: absolute;
  right: 5px;
  cursor: pointer;
  z-index: 3;
}
</style>



